import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { logout } from "../../redux/actions/auth.js";
import CategoryMega from "../MegaDropDown/CategoryMega.jsx";
import Sidebar from "../Sidebar/Sidebar.jsx";
import {
	selectCartItemsCount,
	selectCartItemsTotal,
} from "./../../redux/selectors/cart.js";
import { useTranslation } from "react-i18next";
import CartIcon from "../CartIcon/CartIcon.jsx";
import useWindowSize from "../WindowSize/WindowSize.jsx";
import logo from "./../../icons/sequence-logo.png";

const mapState = (state) => ({
	totalNumCartItems: selectCartItemsCount(state),
	totalAmount: selectCartItemsTotal(state),
	isAuthenticated: state.auth.isAuthenticated,
	user: state.auth.user,
});

function Header({ cartModal, searchModal }) {
	const { t } = useTranslation(["header", "common"]);
	const { totalNumCartItems, totalAmount, isAuthenticated, user } = useSelector(
		mapState
	);
	const dispatch = useDispatch();
	const [width] = useWindowSize();

	return (
		<section className=" sticky-top bg-white rounded">
			<div className="container">
				<div className="row align-items-center py-2">
					{/* Logo Start */}
					<div className="col-4 col-sm-3  col-lg-2">
						<Link
							to="/"
							style={{ letterSpacing: "3px" }}
							className="brand-wrap h2 text-decoration-none text-dark"
						>
							<strong>DAVIDO</strong>
							{/* <img
								className="logo"
								style={{
									maxHeight: "45px",
									maxWidth: "100%",
								}}
								src={logo}
								// src={"https://youla.ru/build/pwa/assets/logo.315c1c.svg"}
								alt="Logo"
							/> */}
						</Link>
					</div>
					{/* Logo End */}

					{/* Middle Links Start */}
					{width > 992 && (
						<div className=" col-lg-6 d-none d-lg-block ">
							<ul className="d-flex justify-content-between align-items-center  list-unstyled mb-0">
								{/* Home */}
								<li>
									<Link
										to="/"
										className="text-dark p-2 pp-active "
										style={{
											fontWeight: 500,
										}}
									>
										{t("header:Home")}
									</Link>
								</li>

								{/* About */}
								<li>
									<Link to="/about" className="text-dark p-2 pp ">
										{t("header:About")}
									</Link>
								</li>
								{/* Categories */}
								<li class="nav-item pp dropdown dropdown-hover position-static ">
									<button
										aria-label="Show Categories"
										class="btn nav-link dropdown-toggle text-dark p-0"
										id="navbarDropdown"
										data-mdb-toggle="dropdown"
										aria-expanded="false"
									>
										{t("header:Categories")}
									</button>
									<div
										class="dropdown-menu w-100 mt-1 bg-white border-0 "
										aria-labelledby="navbarDropdown"
										style={{
											borderRadius: "10px",
											left: "0px",
										}}
									>
										<div
											class="container scroll-y"
											style={{
												maxHeight: "400px",
												overflowY: "scroll",
											}}
										>
											<div class="row my-2">
												<CategoryMega />
											</div>
										</div>
									</div>
								</li>
								{/* Contact */}
								<li>
									<Link to="/contact" className="text-dark p-2 pp">
										{t("header:Contact")}
									</Link>
								</li>
								{/* New Products */}
								<li>
									<Link to="/filter/newproducts" className="text-dark p-2 pp">
										{t("header:New_products")}
									</Link>
								</li>
							</ul>
						</div>
					)}
					{/* Middle Links End */}

					{/* Third links Start */}
					<div className="col-8 col-sm-9  col-lg-4 ms-auto d-inline-flex justify-content-end align-items-center p-0 pe-1">
						{/* Cart */}
						<button
							aria-label="Show Cart"
							onClick={() => cartModal.show()}
							className="btn d-none d-md-inline position-relative text-dark rounded-pill me-2"
						>
							<i className="bi  bi-cart h4 me-2"></i>
							<span
								className="badge rounded-pill bg-danger position-absolute "
								style={{ top: "-3px", left: "23px" }}
							>
								{totalNumCartItems}
							</span>
							<span className="h6 font-weight-bolder">
								{totalAmount.toFixed(2)} {t("common:manat")}
							</span>
						</button>
						{/* search */}
						<button
							to="/"
							aria-label="Show search modal"
							className="btn   pb-2  border-0 "
							onClick={() => searchModal.show()}
							style={{ borderRadius: "30px", paddingTop: "10px" }}
						>
							<i className="bi bi-search h5  text-"></i>
						</button>
						{/* Wishlist */}
						<Link
							to="/wishlist"
							className="btn btn-outline-danger mx-1 border-0"
							style={{ borderRadius: "50px", paddingTop: "10px" }}
						>
							<i className="bi bi-heart-fill  h5"></i>
						</Link>

						{/* Account */}
						<div className="dropdown dropdown-hover">
							<button
								aria-label="Show account dropdown"
								className={`btn dropdown-toggle me-1 me-md-0  no-dropdown-after  ${
									isAuthenticated ? "p-0" : ""
								}`}
								data-bs-toggle="dropdown"
								aria-expanded="false"
								id="dropdownMenuButton1"
								type="button"
								style={{
									borderRadius: "30px",
								}}
							>
								{isAuthenticated ? (
									<img
										alt="profile"
										src={user?.profile_social}
										className="img-fluid"
										style={{ maxHeight: "35px", borderRadius: "15px" }}
									/>
								) : (
									<i className="bi bi-person-fill  h6"></i>
								)}
							</button>
							<ul
								class="dropdown-menu border"
								style={{ left: "-110px", top: "30px", borderRadius: "15px" }}
								aria-labelledby="dropdownMenuButton1"
							>
								{isAuthenticated ? (
									<>
										<li>
											<Link className="dropdown-item" to="/account">
												<i class="bi bi-file-earmark-person me-2"></i>{" "}
												{t("header:Account")}
											</Link>
										</li>
										<li>
											<button
												aria-label="Logout user"
												className="dropdown-item"
												onClick={() => dispatch(logout())}
											>
												<i class="bi bi-card-checklist me-2"></i>
												{t("header:Logout")}
											</button>
										</li>
									</>
								) : (
									<>
										<li>
											<Link className="dropdown-item" to="/login">
												<i class="bi bi-key-fill me-2"></i> {t("header:Login")}
											</Link>
										</li>
										<li>
											<Link className="dropdown-item" to="/signup">
												<i class="bi bi-box-arrow-in-left me-2"></i>{" "}
												{t("header:Register")}
											</Link>
										</li>
									</>
								)}
								{user && user.is_staff === true && (
									<li>
										<Link className="dropdown-item" to="/react-admin">
											<i class="bi bi-key-fill me-2"></i> Admin
										</Link>
									</li>
								)}
							</ul>
						</div>
						{/* menu */}
						{width < 992 && (
							<>
								<button
									aria-label="Show Menu"
									type="button"
									data-bs-toggle="offcanvas"
									data-bs-target="#offcanvasWithBackdrop"
									aria-controls="offcanvasWithBackdrop"
									className="d-inline d-lg-none btn btn-light pb-2  ms-1 me-1"
									style={{ borderRadius: "30px" }}
								>
									<i class="bi bi-list h6"></i>
								</button>
								<Sidebar />
							</>
						)}
					</div>
					{/* Third links End */}
				</div>
			</div>
			{width < 992 && (
				<CartIcon cartModal={cartModal} totalNumCartItems={totalNumCartItems} />
			)}
		</section>
	);
}

export default Header;
