import React, { useState, useEffect } from "react";
import { Link, useLocation } from "react-router-dom";
import SearchInput from "./SearchInput.jsx";
import SearchProduct from "./SearchProduct";
import { Modal } from "bootstrap";

function SearchModal({ searchModal, setSearchModal, searchModalRef }) {
	const [result, setResult] = useState([]);
	const [searchLink, setSearchLink] = useState("");
	const location = useLocation();
	useEffect(() => {
		setSearchModal(new Modal(searchModalRef?.current));
		if (location) {
			searchModal?.hide();
			// closeModalRef?.current?.click();
		}
	}, [location]);

	return (
		<div
			class="modal fade"
			// id="staticBackdrop"
			ref={searchModalRef}
			data-bs-backdrop="static"
			data-bs-keyboard="false"
			tabindex="-1"
			aria-labelledby="staticBackdropLabel"
			aria-hidden="true"
			style={{ top: "50px" }}
		>
			<div class="modal-dialog ">
				<div
					class="modal-content border container "
					style={{
						borderRadius: "10px",
						borderColor: "#6270FB",
					}}
				>
					<div
						class="modal-header  border-0 my-2 bg-white"
						style={{
							borderRadius: "15px",
							// boxShadow: "-5px 5px 16px -4px rgba(0,0,0,0.15)",
							boxShadow: "0 10px 30px 0 rgba(68, 55, 77, 0.25)",
						}}
					>
						<SearchInput
							setSearchLink={setSearchLink}
							setResult={setResult}
							cls="col-11  modal-title"
						/>
						<button
							type="button"
							className="close-btn btn col-1 p-0 px-2  m-0"
							onClick={() => searchModal.hide()}
							data-bs-dismiss="modal"
							// ref={closeModalRef}
							aria-label="Close"
						>
							<i class="bi bi-x-lg"></i>
						</button>
					</div>

					<div
						className="container "
						style={{ maxHeight: "400px", overflowY: "scroll" }}
					>
						<div className="row mx-auto">
							{result?.data?.results.length >= 1 &&
							Array.isArray(result?.data?.results) ? (
								result?.data?.results.map((product) => {
									return <SearchProduct key={product?.id} product={product} />;
								})
							) : (
								<h4 className="text-center my-5">{result}</h4>
							)}
						</div>
						{result?.data?.results.length >= 1 &&
							Array.isArray(result?.data?.results) && (
								<div
									className="row py-2 justify-content-between"
									style={{ fontWeight: "bold" }}
								>
									<div className="col-auto me-auto">
										Total: {result.data.count}
									</div>

									<Link
										to={searchLink}
										className="col-auto text-white  badge bg-danger text-decoration-none"
										style={{ borderRadius: "15px", fontSize: "14px" }}
									>
										See all
									</Link>
								</div>
							)}
					</div>
				</div>
			</div>
		</div>
	);
}

export default SearchModal;
