import {
	ADD_TO_CART,
	REMOVE_CART_ITEM,
	REDUCE_CART_ITEM,
} from "../types/types";

export const addProduct = (size, nextCartItem) => ({
	type: ADD_TO_CART,
	payload: nextCartItem,
	size: size,
});

export const removeCartItem = (size, cartItem) => ({
	type: REMOVE_CART_ITEM,
	payload: cartItem,
	size: size,
});

export const reduceCartItem = (size, cartItem) => ({
	type: REDUCE_CART_ITEM,
	payload: cartItem,
	size: size,
});
