import React, { useEffect, useRef, useState } from "react";

// components
import TopHeader from "../../components/Header/TopHeader.jsx";
import Header from "../../components/Header/Header.jsx";
import SearchModal from "../../components/Search/SearchModal.jsx";
import Footer from "../../components/Footer/Footer.jsx";
import CartModal from "../../components/CartModal/CartModal.jsx";

// Redux
import { useDispatch, useSelector } from "react-redux";
import { checkAuthenticated, load_user } from "../../redux/actions/auth";
import { get_categories } from "../../redux/actions/products.js";
import {
	selectErrors,
	selectSuccessMessages,
} from "../../redux/selectors/messages.js";
import { REMOVE_ERRORS, REMOVE_MESSAGES } from "../../redux/types/types.js";

// i18n
import { useTranslation } from "react-i18next";
// Css
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap-icons/font/bootstrap-icons.css";
import "./../../css/customcss.css";
import "./../../css/Navabar.css";
import "./../../fonts/SfPro/stylesheet.css";

const mapState = (state) => ({
	errors: selectErrors(state),
	successMessages: selectSuccessMessages(state),
});

function Layout(props) {
	const { t, i18n } = useTranslation(["errors", "success"]);
	const dispatch = useDispatch();
	const { errors, successMessages } = useSelector(mapState);
	const [cartModal, setCartModal] = useState(null);
	const [searchModal, setSearchModal] = useState(null);
	const cartModalRef = useRef();
	const searchModalRef = useRef();
	useEffect(() => {
		dispatch(checkAuthenticated());
		dispatch(load_user());
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, []);
	useEffect(() => {
		dispatch(get_categories(i18n.language));
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [i18n.language]);
	return (
		<>
			<TopHeader />
			<Header cartModal={cartModal} searchModal={searchModal} />
			{errors && (
				<div
					class={`alert sticky-top py-3 alert-dismissible fade show alert-danger`}
					role="alert"
					style={{ borderRadius: "10px" }}
				>
					<div className="container d-flex justify-content-between align-items-center">
						<div className="ps-2 d-inline-flex align-items-center">
							<i class="bi bi-exclamation-triangle-fill me-2"></i>

							<strong>{t(`errors:${errors}`)}</strong>
						</div>
						<button
							type="button"
							aria-label="Remove errors"
							class="close-btn btn"
							onClick={() => dispatch({ type: REMOVE_ERRORS })}
						>
							<i class="bi bi-x-lg text-dark"></i>
						</button>
					</div>
				</div>
			)}

			{successMessages && (
				<div
					class={`alert sticky-top py-3 alert-dismissible fade show alert-success`}
					role="alert"
					style={{ borderRadius: "10px" }}
				>
					<div className="container d-flex justify-content-between align-items-center">
						<div className="ps-2 d-inline-flex align-items-center">
							<i class="bi bi-check-circle-fill me-2"></i>

							<strong>{t(`success:${successMessages}`)}</strong>
						</div>
						<button
							type="button"
							aria-label="Remove Messages"
							class="close-btn btn"
							onClick={() => dispatch({ type: REMOVE_MESSAGES })}
						>
							<i class="bi bi-x-lg text-dark"></i>
						</button>
					</div>
				</div>
			)}

			<SearchModal
				searchModal={searchModal}
				setSearchModal={setSearchModal}
				searchModalRef={searchModalRef}
			/>
			<CartModal
				cartModal={cartModal}
				setCartModal={setCartModal}
				cartModalRef={cartModalRef}
			/>
			<div>{props.children}</div>
			<Footer />
		</>
	);
}

export default Layout;
