import React from "react";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import {
	selectCartItemsTotal,
	selectCartItems,
} from "./../../redux/selectors/cart.js";
import CartProduct from "./CartProduct.jsx";
import { Modal } from "bootstrap";
import { useEffect } from "react";

const mapState = (state) => ({
	cartItems: selectCartItems(state),
	totalAmount: selectCartItemsTotal(state),
});

function CartModal({ cartModal, setCartModal, cartModalRef }) {
	const closeModalRef = React.createRef();
	const { cartItems, totalAmount } = useSelector(mapState);
	const handleClick = () => {
		cartModal.hide();
		// closeModalRef.current.click();
	};
	useEffect(() => {
		setCartModal(new Modal(cartModalRef.current));
	}, []);
	const { t } = useTranslation(["cart", "common"]);
	return (
		<div
			class="modal fade"
			// id="exampleModal2"
			tabindex="-1"
			data-bs-backdrop="static"
			data-bs-keyboard="false"
			ref={cartModalRef}
			aria-labelledby="exampleModalLabel"
			aria-hidden="true"
		>
			<div
				style={{ borderRadius: "30px", border: "0" }}
				class="modal-dialog   modal-dialog-scrollable modal-fullscreen-sm-down"
			>
				<div class="modal-content container " style={{ background: "#F2F5F7" }}>
					<div
						class="modal-header border-0 my-2 bg-white"
						style={{
							borderRadius: "15px",
							boxShadow: "0 10px 30px 0 rgba(68, 55, 77, 0.3)",
						}}
					>
						<h3
							class="modal-title"
							style={{ fontWeight: "bold" }}
							id="exampleModalLabel"
						>
							{t("Cart")}
						</h3>
						<button
							type="button"
							class="close-btn btn"
							data-bs-dismiss="modal"
							aria-label="Close"
							onClick={() => cartModal.hide()}
							// ref={closeModalRef}
						>
							<i class="bi bi-x-lg"></i>
						</button>
					</div>
					<div class="modal-body ">
						{cartItems.map((item) => {
							return (
								<CartProduct key={item.id} modalref={handleClick} item={item} />
							);
						})}
						<hr />
						<div className="d-flex justify-content-between">
							<div className="h5">{t("Subtotal")}</div>
							<div className="h4">
								<strong>{totalAmount.toFixed(2)}</strong> {t("common:manat")}
							</div>
						</div>
					</div>
					<div
						class="modal-footer bg-white my-2"
						style={{
							borderRadius: "15px",
						}}
					>
						<Link
							onClick={() => handleClick()}
							to="/checkout"
							className={`text-decoration-none btn btn-primary w-100 ${
								cartItems.length < 1 && "disabled"
							}`}
							style={{ borderRadius: "15px" }}
						>
							<span>{t("Continue_to_Checkout")}</span>
						</Link>
					</div>
				</div>
			</div>
		</div>
	);
}

export default CartModal;
