import React from "react";
import { useTranslation } from "react-i18next";

const lngs = { ru: "Русский", en: "English", tk: "Turkmen" };

function LanguageIcon({ top, iconStyle }) {
	const { t, i18n } = useTranslation(["topheader"]);
	return (
		<>
			<span
				className={`btn dropdown-toggle no-dropdown-after ${
					top ? "text-white" : "text-dark"
				} h5-5 ps-0`}
				data-bs-toggle="dropdown"
				aria-expanded="false"
			>
				<span className={top ? "pe-3" : "d-none d-md-inline pe-3"}>
					{lngs[i18n.language]}
				</span>
				<i className="bi bi-globe" style={iconStyle && iconStyle}></i>
			</span>
			<div
				className="dropdown-menu"
				style={{
					minWidth: "6rem",
					borderRadius: ".5rem",
					zIndex: "1024",
				}}
			>
				<li
					className="dropdown-item px-2"
					role="button"
					onClick={() => i18n.changeLanguage("tk")}
				>
					{t("Turkmen")}
				</li>
				<li
					className="dropdown-item px-2"
					role="button"
					onClick={() => i18n.changeLanguage("ru")}
				>
					{t("Russian")}
				</li>
				<li
					className="dropdown-item px-2"
					role="button"
					onClick={() => i18n.changeLanguage("en")}
				>
					{t("English")}
				</li>
			</div>
		</>
	);
}

export default LanguageIcon;
