import React from "react";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { selectCategories } from "../../redux/selectors/products.js";

const mapState = (state) => ({
	categories: selectCategories(state),
});

function CategoryMega() {
	const { categories } = useSelector(mapState);

	return (
		<>
			{categories.map((category) => {
				return (
					<div
						className="col-auto border-0 m-1 pp"
						style={{ borderRadius: "10px" }}
					>
						<Link
							to={`/category/${category?.slug}`}
							className="btn text-decoration-none text-dark p-1 ps-0"
						>
							<img
								alt="Category"
								src={category?.picture}
								className="img-fluid me-2"
								style={{
									maxHeight: "30px",
									borderRadius: "10px",
								}}
							/>
							{category?.name}
						</Link>
					</div>
				);
			})}
		</>
	);
}

export default CategoryMega;
