import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, useLocation } from "react-router-dom";
import { selectCategories } from "../../redux/selectors/products";
import "./Sidebar.css";
import tkm from "./../../icons/tkm.png";
import rus from "./../../icons/rus.png";
import eng from "./../../icons/eng.png";
import { useTranslation } from "react-i18next";
import { logout } from "../../redux/actions/auth";

const mapState = (state) => ({
	categories: selectCategories(state),
	isAuthenticated: state.auth.isAuthenticated,
});

function Sidebar() {
	const { t, i18n } = useTranslation(["header", "topheader"]);
	const closeModalRef = React.createRef();
	const { categories, isAuthenticated } = useSelector(mapState);
	const [closeModal, setCloseModal] = useState(false);
	const location = useLocation();
	const dispatch = useDispatch();
	const changeLanguage = (name) => {
		i18n.changeLanguage(name);
		setCloseModal(true);
	};
	useEffect(() => {
		if (location) {
			closeModalRef?.current?.click();
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [location, closeModal]);
	return (
		<div
			class="offcanvas offcanvas-end"
			tabIndex="-1"
			id="offcanvasWithBackdrop"
			aria-labelledby="offcanvasWithBackdropLabel"
		>
			<div
				class="offcanvas-header ps-5 border-0 mt-1 mb-0 bg-white h2 text-decoration-none text-dark"
				style={{
					borderRadius: "20px",
					letterSpacing: "3px",
					boxShadow: "10px 11px 16px -4px rgba(0,0,0,0.15)",
				}}
			>
				<strong>DAVIDO</strong>

				<button
					type="button"
					class="close-btn btn text-reset p-2"
					data-bs-dismiss="offcanvas"
					aria-label="Close"
					ref={closeModalRef}
				>
					<i class="bi bi-x-lg"></i>
				</button>
			</div>
			<div class="offcanvas-body">
				<div class="flex-shrink-0  ">
					<ul class="list-unstyled ps-0">
						{/* Home */}
						<li class="mb-1">
							<button
								class="btn btn-toggle align-items-center rounded collapsed "
								data-bs-toggle="collapse"
								data-bs-target="#home-collapse"
								aria-expanded="true"
								style={{ fontSize: "18px" }}
							>
								<strong>{t("header:Home")}</strong>
							</button>
							<div class="collapse show" id="home-collapse">
								<ul
									class="btn-toggle-nav list-unstyled fw-normal mt-2 pb-1 small ps-2"
									style={{ fontSize: "16px" }}
								>
									<li>
										<Link to="/contact" class="link-dark rounded">
											<i class="bi bi-shop me-2 "></i> {t("header:Contact")}
										</Link>
									</li>
									<li>
										<Link to="/about" class="link-dark rounded">
											<i class="bi bi-info-circle me-2 text-primary"></i>
											{t("header:About")}
										</Link>
									</li>
									<li>
										<Link to="/wishlist" class="link-dark rounded">
											<i class="bi bi-heart-fill me-2 text-danger"></i>{" "}
											{t("header:Wishlist")}
										</Link>
									</li>
								</ul>
							</div>
						</li>
						{/* Categories */}
						<li class="mb-1">
							<button
								class="btn btn-toggle align-items-center rounded collapsed"
								data-bs-toggle="collapse"
								data-bs-target="#dashboard-collapse"
								aria-expanded="false"
								style={{ fontSize: "18px" }}
							>
								<strong> {t("header:Categories")}</strong>
							</button>
							<div class="collapse" id="dashboard-collapse">
								<ul
									class="btn-toggle-nav list-unstyled fw-normal pb-1 small mt-2"
									style={{ fontSize: "16px" }}
								>
									{Array.isArray(categories) &&
										categories.length >= 1 &&
										categories.map((category) => {
											return (
												<li key={category?.id}>
													<Link
														to={`/category/${category?.slug}`}
														class="link-dark rounded"
													>
														<img
															alt="Category"
															src={category?.picture}
															className="img-fluid me-3"
															style={{
																maxHeight: "35px",
																borderRadius: "15px",
															}}
														/>
														{category?.name}
													</Link>
												</li>
											);
										})}
								</ul>
							</div>
						</li>
						{/* Orders */}
						<li class="mb-1">
							<button
								class="btn btn-toggle align-items-center rounded collapsed"
								data-bs-toggle="collapse"
								data-bs-target="#orders-collapse"
								aria-expanded="false"
								style={{ fontSize: "18px" }}
							>
								<strong>{t("header:Products")}</strong>
							</button>
							<div class="collapse" id="orders-collapse">
								<ul
									class="btn-toggle-nav list-unstyled fw-normal pb-1 small mt-1 ps-2"
									style={{ fontSize: "16px " }}
								>
									<li>
										<Link to="/filter/newproducts" class="link-dark rounded">
											{t("header:New_products")}
										</Link>
									</li>
									<li>
										<Link
											to="/filter/discountProducts"
											class="link-dark rounded"
										>
											{t("header:Arzanladasyk")}
										</Link>
									</li>
									<li>
										<Link
											to="/filter/expensiveProducts"
											class="link-dark rounded"
										>
											{t("header:In Gymmat Harytlar")}
										</Link>
									</li>
								</ul>
							</div>
						</li>
						{/* Divider */}
						<li class="border-top my-3"></li>
						{/* Account */}
						<li class="mb-1">
							<button
								class="btn btn-toggle align-items-center mb-1 rounded collapsed"
								data-bs-toggle="collapse"
								style={{ fontSize: "18px" }}
								data-bs-target="#account-collapse"
								aria-expanded="false"
							>
								<strong>{t("header:Account")}</strong>
							</button>
							<div class="collapse" id="account-collapse">
								<ul
									class="btn-toggle-nav list-unstyled fw-normal pb-1 small ps-2"
									style={{ fontSize: "16px" }}
								>
									{isAuthenticated ? (
										<>
											<li>
												<Link to="/account" class="text-dark rounded mb-1">
													<i class="bi bi-file-earmark-person me-2"></i>{" "}
													{t("header:Account")}
												</Link>
											</li>
											<li>
												<Link to="/account" class="link-dark rounded">
													<i class="bi bi-card-checklist me-2"></i>
													{t("header:Orders")}
												</Link>
											</li>

											<li
												class="link-dark rounded"
												onClick={() => dispatch(logout())}
											>
												<i class="bi bi-box-arrow-left me-2"></i>{" "}
												{t("header:Logout")}
											</li>
										</>
									) : (
										<>
											<li>
												<Link to="/login" class="link-dark rounded">
													<i class="bi bi-key-fill me-2"></i>{" "}
													{t("header:Login")}
												</Link>
											</li>
											<li>
												<Link to="/signup" class="link-dark rounded">
													<i class="bi bi-box-arrow-in-left me-2"></i>
													{t("header:Register")}
												</Link>
											</li>
										</>
									)}
								</ul>
							</div>
						</li>
						{/* Languages */}
						<li class="mb-1 mt-3 d-flex align-items-center ">
							<button
								class="btn h5  rounded collapsed me-2"
								onClick={() => changeLanguage("tk")}
							>
								<img
									alt="Turkmen"
									src={tkm}
									className="img-fluid mb-2"
									style={{ borderRadius: "5px" }}
								/>{" "}
								<div>{t("topheader:Turkmen")}</div>
							</button>
							<button
								onClick={() => changeLanguage("ru")}
								class="btn h5  rounded collapsed me-2"
							>
								<img
									alt="Russian"
									src={rus}
									className="img-fluid mb-2"
									style={{ borderRadius: "5px" }}
								/>{" "}
								<div>{t("topheader:Russian")}</div>
							</button>
							<button
								onClick={() => changeLanguage("en")}
								class="btn h5  rounded collapsed me-2"
							>
								<img
									alt="English"
									src={eng}
									className="img-fluid mb-2"
									style={{ borderRadius: "5px" }}
								/>{" "}
								<div>{t("topheader:English")}</div>
							</button>
						</li>
					</ul>
				</div>
			</div>
		</div>
	);
}

export default Sidebar;
