import React, { useState, useEffect, useRef } from "react";
import { search } from "../../redux/actions/products";
import { useDispatch, useSelector } from "react-redux";
import { useTranslation } from "react-i18next";
import { Link } from "react-router-dom";

const mapState = ({ products }) => ({
	categories: products.categories,
});

function SearchInput({ cls, setResult, setSearchLink }) {
	const { t, i18n } = useTranslation(["common"]);
	// form
	const [selectedCategory, setSelectedCategory] = useState({
		value: "",
		name: "All",
	});
	const linkRef = useRef(null);
	const [searchValue, setSearchValue] = useState("");
	const [sentRequest, setSentRequest] = useState(false);
	// redux
	const dispatch = useDispatch();
	const { categories } = useSelector(mapState);

	// choose category change
	const handleCategoryChange = (categoryValue, categoryName) => {
		setSelectedCategory({
			value: categoryValue,
			name: categoryName,
		});
	};

	const getConfi = () => {
		return `category_id=${
			selectedCategory.value
		}&search=${searchValue.trim()}&limit=10`;
	};

	const handleSubmit = (e) => {
		e.preventDefault();
		linkRef.current.click();
	};

	useEffect(() => {
		if (searchValue.trim().length >= 3) {
			setSentRequest(true);
			const confi = getConfi();
			dispatch(search(confi, setSentRequest, i18n.language)).then((result) => {
				setResult(result);
				setSearchLink(
					`/search?category=${selectedCategory.name}&query=${searchValue}`
				);
			});
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [searchValue, selectedCategory]);

	return (
		<>
			<form onSubmit={(e) => handleSubmit(e)} className={cls}>
				<div class="input-group  position-relative">
					{/* Category Select */}
					<button
						type="button"
						className="btn dropdown-toggle bg-primary text-white"
						data-bs-toggle="dropdown"
						style={{
							outline: "none",
							borderBottomLeftRadius: "0.75rem",
							borderTopLeftRadius: "0.75rem",
							borderTopRightRadius: "0",
							borderBottomRightRadius: "0",
						}}
						aria-expanded="false"
					>
						{selectedCategory?.name && selectedCategory?.name === "All"
							? t("common:All")
							: selectedCategory?.name}
					</button>
					<ul
						className="dropdown-menu scroll-y"
						style={{
							maxHeight: "300px",
							overflowY: "scroll",
							maxWidth: "100px !important",
							borderRadius: "15px",
						}}
					>
						<li
							className="dropdown-item"
							role="button"
							onClick={() => handleCategoryChange("", "All")}
						>
							{t("common:All")}
						</li>
						{categories.map((category) => {
							return (
								<li
									key={category.id}
									className="dropdown-item truncate-overflow-1 "
									role="button"
									onClick={() =>
										handleCategoryChange(category.id, category.name)
									}
								>
									{category.name}
								</li>
							);
						})}
					</ul>
					{/* Input */}
					<input
						type="text"
						className="form-control "
						style={{ borderLeft: "none" }}
						aria-label="Search"
						placeholder={t("common:Search")}
						value={searchValue}
						onChange={(e) => setSearchValue(e.target.value)}
					/>

					{/* Search Button */}
					<Link
						to={`/search?category=${selectedCategory.name}&query=${searchValue}`}
						className="btn btn-primary text-decoration-none"
						ref={linkRef}
						style={{
							borderTopRightRadius: "0.75rem",
							borderBottomRightRadius: "0.75rem",
						}}
					>
						{sentRequest ? (
							<span class="spinner-border spinner-border-sm" role="status">
								<span class="visually-hidden">Loading...</span>
							</span>
						) : (
							<i className="bi bi-search"></i>
						)}
					</Link>
				</div>
			</form>
		</>
	);
}

export default SearchInput;
