import React from "react";
import { useDispatch } from "react-redux";
import {
	addProduct,
	reduceCartItem,
	removeCartItem,
} from "./../../redux/actions/cart";
import { Link } from "react-router-dom";

function CartProduct({ item, modalref }) {
	const dispatch = useDispatch();

	return (
		<div className="row my-3">
			<div className="col-3">
				<Link
					onClick={() => modalref()}
					to={`/product/${item.slug}/${item.id}`}
					className="text-decoration-none text-dark"
				>
					<img
						alt="cart-product"
						className="img-fluid"
						style={{ borderRadius: "15px" }}
						src={item?.picture_search}
					/>
				</Link>
			</div>
			<div className="info col-6">
				<Link
					to={`/product/${item.slug}/${item.id}`}
					onClick={() => modalref()}
					className="text-decoration-none text-dark"
				>
					<h5 className="truncate-overflow-1">
						{item?.size.size_name} - {item?.name}
					</h5>
					<div className="h6 truncate-overflow-1 font-weight-bolder">
						{item.new_price} manat
					</div>
				</Link>
			</div>
			<div className="col-1 d-flex justify-content-center align-items-center">
				<button
					className="btn"
					onClick={() => dispatch(removeCartItem(item.size, item))}
					type="button"
				>
					<i class="bi bi-trash  h5"></i>
				</button>
			</div>
			<div className="price d-flex flex-column  col-2">
				<div
					class="btn-group-vertical  "
					role="group"
					aria-label="Basic example"
				>
					<button
						type="button"
						class="btn btn-primary"
						style={{
							maxWidth: "35px",
							padding: 1,
							borderTopLeftRadius: "15px",
							borderTopRightRadius: "15px",
						}}
						onClick={() => {
							dispatch(addProduct(item.size, item));
						}}
					>
						<i class="bi bi-plus"></i>
					</button>

					<button
						type="button"
						class="btn btn-primary "
						style={{ maxWidth: "35px", padding: 1 }}
					>
						{item?.quantity}
					</button>

					<button
						type="button "
						style={{
							maxWidth: "35px",
							padding: 1,
							borderBottomLeftRadius: "15px",
							borderBottomRightRadius: "15px",
						}}
						class="btn btn-primary"
						onClick={() => dispatch(reduceCartItem(item.size, item))}
					>
						<i class="bi bi-dash"></i>
					</button>
				</div>
			</div>
		</div>
	);
}

export default CartProduct;
